
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import {mapState, mapMutations, mapGetters} from 'vuex'
import {hidMobile} from 'js/mixin'
import {mainRouter} from '@/router/index'
import http from 'js/http'
import psw from 'components/psw/index'

export default {
name: "personal",
components: {
Group,
CellBox,
psw
},
computed: {
identifyPath() {
if (!this.identifyMsg) return ''
if (!this.identifyMsg.isIdentify) return 'submit'
switch (this.identifyMsg.status) {
case 0:
return 'wait'
case 1:
return 'success'
case 2:
return 'fail'
}

},
nodeTypeId() {
if (!this.nodeInfo.typeId) return 1
return this.nodeInfo.typeId

},
nodePath() {

if (!this.nodeInfo) return ''
switch (this.nodeInfo.status) {
case 2:
return 'wait'
case 4:
return 'fail'
}

},
...mapGetters([
"loginMsg",
'myNodeInfo'
]),
},
methods: {
activateNode(){
if (!this.identifyMsg.isIdentify||this.identifyMsg.status!==1){
this.$vux.toast.show('请先完成实名认证')
return
}
http.post('/node/activation', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.$vux.toast.show({
text: res.msg,
type: 'success'
})
setTimeout(()=>{
this.getNodeInfo()
},500)

})
},
getNodeInfo() {
http.post('/node/info', {}, (res) => {

if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.nodeInfo = res.content
localStorage.setItem("myNodeInfo", JSON.stringify(res.content));
this.setMyNodeInfo(res.content)
})
},
getIdentifyMsg() {
http.post('/identify', {}, (res) => {

if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
let identify = {}
if (res.content === '') {
identify.isIdentify = false
} else {
identify = res.content
identify.isIdentify = true
}
this.identifyMsg = identify
localStorage.setItem("identifyMsg", JSON.stringify(identify));
this.setIdentifyMsg(identify)
})
},
pageInt() {
if (!this.loginMsg) {
this.$router.push({
path: `/login`
})
} else {
this.getNodeInfo()
this.identifyMsg = {}
this.getIdentifyMsg()
}

},
...mapMutations({
setIdentifyMsg: 'IDENTIFY_MSG',
setMyNodeInfo: 'MY_NODE_INFO'
})
},
data() {
return {
hidMobile: hidMobile,
personalRouter: mainRouter[2],
nodeInfo: {
typeId:''
},
identifyMsg: {}
}
},
created() {
this.pageInt()
},
activated() {

},
watch: {
'$route': function (t, f) {
if (t.path === '/personal') {
this.pageInt()
}
}
},
}



